    handletouchmove: function (event) {
      var that = this;
      var currentX = event.touches[0].pageX;
      var currentY = event.touches[0].pageY;
      var tx = currentX - this.lastX;
      var ty = currentY - this.lastY;
      var scrollHeight = that.scrollHeight;
      var page = that.page;
      var rpage_num = that.rpage_num;

      if (Math.abs(tx) > Math.abs(ty)) {
        if (tx < 0) {// text = "向左滑动"
        } else if (tx > 0) {// text = "向右滑动"
        }
      } else {
        //上下方向滑动
        if (ty < 0 && !that.is_reloading) {
          // text = "向上滑动"
          if (that.page <= that.rpage_num  ) {
            //将当前坐标进行保存以进行下一次计算
            that.getMoreGoodsTapTag();
            /*
            if (currentY > scrollHeight - 100) {
              
            }
            */
          }
        } else if (ty > 0) {//text = "向下滑动"
        }
      }

      that.setData({
        floorstatus: true
      });
      that.lastX = currentX;
      that.lastY = currentY;
	  console.log('currentX:', currentX, 'currentY:', currentY, 'ty:', ty, ' page:', page, ' rpage_num:', that.rpage_num,' is_reloading:', that.is_reloading)
    },
    handletouchstart: function (event) {
      // console.log(event)
      // 赋值
      this.lastX = event.touches[0].pageX;
      this.lastY = event.touches[0].pageY;
      this.setData({
        touchstop: false
      });
    },
    handletouchend: function (event) {
      var that = this;
      var currentY = that.lastY;
      this.setData({
        touchstop: true
      }); //console.log('滑动停止 currentX:', that.data.lastX, 'currentY:', that.data.lastY, that.data.scrollHeight)
    },
    getMoreGoodsTapTag: function (e) {
      var that = this;
      var page = that.page  ;
      var rpage_num = that.rpage_num;
      var is_reloading = that.is_reloading;
      console.log('getMoreGoodsTapTag 加载更多中，请稍等 page:', page, 'is_reloading:', is_reloading, that.scrollHeight);

      if (is_reloading) {
        return;
      }

      if (page > rpage_num && page !=1) {
        wx.showToast({
          title: '已经到底了~',
          icon: 'none',
          duration: 1000
        });
        that.setData({
          loadingHidden: false,
          loading_note: '已经到底了~'
        });
        setTimeout(function () {
          that.setData({
            loadingHidden: true
          });
        }, 1000);
        return;
      }
      /*
      wx.showToast({
        title: '加载中',
        icon: 'loading',
        duration: 500
      })
      */

      that.setData({
        //page: page+1,
        loadingHidden: false,
        loading_note: '加载中'
      });
      that.reloadData();
    },

