    //图片加载出错，替换为默认图片
    imageError: function (e) {
      var that = this;
      var errorImgIndex = e.target.dataset.imageindex;
      var default_img = that.default_img;
      var imgObject = "recommentslist_show[" + errorImgIndex + "].image"; //commentList为数据源，对象数组

      var errorImg = {};
      errorImg[imgObject] = default_img; //构建一个对象

      that.setData(errorImg);
      console.log('hall imageError():', errorImg);
    },
    getScrollHeight: function () {
      var that = this;
      wx.createSelectorQuery().select('#venues_box').boundingClientRect(rect => {
        if (rect) {
          that.setData({
            scrollHeight: rect.height //loadingHidden:true

          });
          console.log('页面实际高度:', that.scrollHeight);
        } else {
          setTimeout(function () {
            that.getScrollHeight();
          }, 300);
        }
      }).exec();
    },
    BuyModeChange: function (e) {
      console.log(e.detail.value);
	  var that = this
      var obj = {};
	  //obj['buyhidden2'] = e.detail.value; 
	  that.buyhidden1 =  e.detail.value ;
    },
    setData: function (obj) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }
          data = data[key2];
        });
      });
    }
  }
